.shiye-btn {
  display: inline-block;
  height: 28px;
  padding: 0 15px;
  line-height: 28px;
  font-size: 12px;
  border-radius: 2px;
  border: 1px solid #bbb;
  vertical-align: middle;
  -webkit-transition: color, border-color, background-color;
  transition: color, border-color, background-color;
  -webkit-transition-duration: 200ms;
          transition-duration: 200ms;
  -webkit-transition-timing-function: linear;
          transition-timing-function: linear;
  cursor: pointer;
  color: #333;
  box-sizing: border-box;
}
.shiye-btn + .shiye-btn {
  margin-left: 10px;
}
.shiye-btn:hover {
  border-color: #38f;
  color: #38f;
}
.shiye-btn:focus {
  outline: 0;
}
.shiye-btn.block {
  display: block;
  width: 100%;
}
.shiye-btn.primary {
  color: #fff;
  background-color: #38f;
  border-color: #38f;
}
.shiye-btn.primary:hover {
  background-color: #59f;
  border-color: #59f;
}
.shiye-btn.outline,
.shiye-btn.primary.outline {
  background-color: #fff;
  border-color: #38f;
  color: #38f;
}
.shiye-btn.outline:hover,
.shiye-btn.primary.outline:hover {
  border-color: #59f;
  color: #59f;
}
.shiye-btn.danger {
  background-color: #f44;
  border-color: #f44;
  color: #fff;
}
.shiye-btn.danger:hover {
  background-color: #f66;
  border-color: #f66;
}
.shiye-btn.danger.outline {
  background-color: #fff;
  color: #f44;
}
.shiye-btn.danger.outline:hover {
  color: #f66;
  border-color: #f66;
}
.shiye-btn.success {
  background-color: #4b0;
  border-color: #4b0;
  color: #fff;
}
.shiye-btn.success:hover {
  background-color: #6c2;
  border-color: #6c2;
}
.shiye-btn.success.outline {
  background-color: #fff;
  color: #4b0;
}
.shiye-btn.success.outline:hover {
  border-color: #6c2;
  color: #6c2;
}
.shiye-btn.loading {
  cursor: not-allowed;
  position: relative;
}
.shiye-btn.loading .shiye-btn__name {
  visibility: hidden;
}
.shiye-btn.loading:hover {
  border-color: #bbb;
}
.shiye-btn.loading:after {
  content: "";
  border: 3px solid;
  border-color: #666 transparent transparent;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  margin-left: -8px;
  margin-top: -8px;
  border-radius: 50%;
  box-sizing: border-box;
  -webkit-animation: rotate 0.6s linear infinite;
          animation: rotate 0.6s linear infinite;
}
.shiye-btn.disabled {
  background-color: #f8f8f8;
  border-color: #e5e5e5;
  color: #bbb;
  cursor: not-allowed;
}
.shiye-btn.large {
  height: 38px;
  line-height: 36px;
  font-size: 14px;
  padding: 0 23px;
}
.shiye-btn.small {
  height: 26px;
  line-height: 24px;
  padding: 0 7px;
}
.shiye-btn.no-border {
  border: 0;
}
.shiye-btn.link .shiye-btn__name {
  color: #333;
  -webkit-transition: color, border-color, background-color;
  transition: color, border-color, background-color;
  -webkit-transition-duration: 200ms;
          transition-duration: 200ms;
  -webkit-transition-timing-function: linear;
          transition-timing-function: linear;
}
.shiye-btn.link:hover .shiye-btn__name {
  color: #38f;
}
.shiye-btn.primary .shiye-icon:before {
  color: #fff;
}
.shiye-btn .shiye-icon:before {
  vertical-align: -2px;
  color: #333;
  margin: 0 2px;
}
.shiye-btn-group + .shiye-btn-group {
  margin-left: 10px;
}
.shiye-btn-group .primary {
  border-left: 1px solid #59f;
}
.shiye-btn-group .shiye-btn {
  margin-left: 0;
}
.shiye-btn-group .shiye-btn + .shiye-btn {
  margin-left: -1px;
  border-radius: 0;
}
.shiye-btn-group .shiye-btn:first-child {
  border-radius: 2px 0 0 2px;
}
.shiye-btn-group .shiye-btn:last-child {
  border-radius: 0 2px 2px 0;
}
@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
