/* appear/disapear: 遮罩的透明度变化动效 */
.shiye-sweetalert-container {
  -webkit-animation-name: appear;
          animation-name: appear;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  -webkit-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
          align-items: center;
  -webkit-animation-duration: 80ms;
          animation-duration: 80ms;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.shiye-sweetalert-container--disappear {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  -webkit-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
          align-items: center;
  -webkit-animation-duration: 80ms;
          animation-duration: 80ms;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-name: disappear;
          animation-name: disappear;
}
.shiye-sweetalert-container--hidden {
  display: none;
}
.shiye-sweetalert-mount-point {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  -webkit-box-pack: center;
          justify-content: center;
  -webkit-box-align: center;
          align-items: center;
  display: -webkit-box;
  display: flex;
}
.shiye-sweetalert {
  display: inline-block;
  box-sizing: border-box;
  min-width: 450px;
  max-width: 75%;
  border-radius: 4px;
  background-color: #fff;
  -webkit-transition: -webkit-transform 200ms ease-out;
  transition: -webkit-transform 200ms ease-out;
  transition: transform 200ms ease-out;
  transition: transform 200ms ease-out, -webkit-transform 200ms ease-out;
  padding: 20px;
}
.shiye-sweetalert__header {
  padding-bottom: 10px;
  border-bottom: 1px solid #e5e5e5;
  margin: 0;
  box-sizing: border-box;
  margin-bottom: 20px;
}
.shiye-sweetalert__footer {
  text-align: right;
  margin-top: 30px;
}
@-webkit-keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes disappear {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes disappear {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
